import './queEsCAS.css';


const QueEsCAS = () => {
  return (
    <div className='queEsCAS'>
      <h1 className='cabecera'>QU ES CAS?</h1>
      <p className='parrafo'>Uno de los programas ms populares del Bachillerato Internacional (IB) es el Programa del Diploma (IBDP), un programa educativo de dos aos para estudiantes de 16 a 18 aos. Este programa est formado por los 3 componentes principales: Ensayo Extendido (EE), Teora del Conocimiento (TOK) y Creatividad, Accin y Servicio (CAS). Pero, qu significa CAS?</p>

      <p className='parrafo'>CAS se cursa a lo largo del Programa del Diploma y hace participar a los alumnos en una variedad de actividades simultneas al estudio de las disciplinas acadmicas.</p>

      <p className='parrafo'>CAS no se evala formalmente, sino que los alumnos reflexionan sobre sus experiencias de CAS como parte del PD y aportan pruebas de haber alcanzado los siete resultados del aprendizaje.</p>

      <h1 className='cabecera'>Cul es la estructura de CAS?</h1>

      <p className='parrafo'>Las tres reas de la estructura de CAS estn relacionadas entre s con actividades especficas para alentar a los alumnos a hacer conexiones entre las diferentes materias.</p>

      <ul className='puntos'>
        <li className='punto'>
          <b>CREATIVIDAD. </b> Por un lado, la ampliacin y exploracin de ideas que llevan a un pensamiento o actuacin creativa, original o interpretativa.
        </li>
        <li className='punto'>
          <b>ACCIN O ACTIVIDAD. </b> Tambin, actividades que conllevan un desafo o esfuerzo fsico que ayudan a mantener un estilo de vida saludable y complementan las tareas acadmicas.
        </li>
        <li className='punto'>
          <b>SERVICIO. </b> Adems, un intercambio colaborativo con la comunidad en respuesta a una necesidad real, lo que para el alumno significa un aprendizaje.
        </li>
      </ul>

      <p className='parrafo'>Una experiencia de CAS debe encajar en una o varias de las tres reas. Por un lado, basarse en alguna habilidad o inters. Por otro lado, dar la oportunidad de desarrollar los atributos de la comunidad de aprendizaje del Bachillerato Internacional. Finalmente, no usarse en las tareas que realicen los alumnos en las asignaturas del PD.</p>

      <p className='parrafo'>Como resultado, el programa de CAS culmina al alcanzar siete objetivos principales. As, algunos de ellos, pueden incluso lograrse en varias ocasiones, y mediante la carpeta CAS los alumnos muestran al colegio pruebas de sus logros.</p>

      <ul className='puntos'>
        <li className='punto'>
          Identificar las <b>habilidades fuertes y dbiles</b> en uno mismo.
        </li>
        <li className='punto'>
          Mostrar que se han afrontado desafos y que se han desarrollado <b>nuevas habilidades</b>.
        </li>
        <li className='punto'>
          <b>Capacidad</b> de articular las etapas de una experiencia de CAS.
        </li>
        <li className='punto'>
          Mostrar <b>perseverancia y compromiso</b> en las experiencias.
        </li>
        <li className='punto'>
          <b>Capacidad</b> de trabajo en equipo y reconocimiento de sus ventajas.
        </li>
        <li className='punto'>
          <b>Compromiso</b> en cuestiones internacionales.
        </li>
        <li className='punto'>
          Conciencia de las <b>consecuencias de las decisiones</b>.
        </li>
      </ul> 

      <h1 className='cabecera'>CUL ES LA IMPORTANCIA DE CAS?</h1>

      <p className='parrafo'>CAS permite a los alumnos potenciar su desarrollo personal e interpersonal a travs del aprendizaje mediante la experiencia. Tambin les brinda oportunidades para la autodeterminacin y la colaboracin con otras personas, a la vez que fomenta una sensacin de satisfaccin y disfrute de su trabajo. Al mismo tiempo, CAS tiene la importante funcin de contrarrestar la presin acadmica del PD.</p>

      <p className='parrafo'>Un buen programa de CAS debe ser ameno y a la vez constituir un desafo, un camino hacia el descubrimiento personal. Aunque cada alumno tiene un punto de partida diferente y, por tanto, diferentes metas y necesidades, para la mayora, las actividades de CAS constituyen experiencias profundas y transformadoras, de gran trascendencia para sus vidas.</p>
    </div>
  );
}

export default QueEsCAS;
