const isVideo = (slideImage) => {
    if(slideImage == slideImagesCarrera[0]){
        return <img src={slideImagesCarrera[0]} alt={slideImagesCarrera[0]} className='actSlideImg' />              
    }else{
        return  <div className='actSlideVideo'>
                    <ReactPlayer 
                    url={slideImagesCarrera[1]}
                    pip={true}
                    controls={true}
                    playing={false}
                    />
                </div>
    }
}

const Actividad = () => {
    return (
        <div className='actividad'>
        <h1 className='actTitulo'>ACTIVIDAD</h1>

            <h1>VOLUNTARIADO COMO "COLABORADORA EN LOS SALESIANOS MANUEL LORA TAMAYO"</h1>
            <div className='act'>
                <p className='actParrafo'> Esta experiencia consiste en asistir todos los viernes de 4 a 9 de la noche y realizar diferentes talleres, gymkhanas y juegos para nios de entre 0 y 14 aos.Lo realic en el colegio Salesianos Manuel Lora Tamayo desde septiembre de 2021 hasta junio de 2022 todos los viernes exentos festivos. Es una forma de que los nios se entretengan, relacionen y adquieran cualidades de forma gratuita adems de tener numerosos regalos y sorpresas con los que tendrn gran ilusin por volver y pasar una buena tarde. Realizamos numerosas actividades temticas como casas del terror, competiciones de ftbol Esta experiencia me ha servido para indagar ms en el comportamiento de los nios pequeos y para darme cuenta de lo difcil que es a veces entenderles o que te entiendan, a pesar de ello, ha sido una experiencia nica en la que he aprendido muchas cualidades gracias a los nios como son la paciencia, el compaerismo e incluso expresar los sentimientos.</p>
                <div className="actSlideContainer">
                    <Fade infinite={true}>
                        {slideImagesVolun.map((slideImage, index)=> (
                            <div key={index}  className="actSlide">
                                <img src={slideImage} alt={slideImage} className='actSlideImg' />
                            </div>
                        ))} 
                    </Fade>
                </div>
            </div>

            <h1>VOLUNTARIADO EN LA PERRERA</h1>
            <div className='act'>
                <p className='actParrafo'>Esta experiencia se trata de un da en el que fuimos a la perrera para ver la realidad de esta y en qu condiciones llegan algunos animales al lugar. Fue bastante impactante ya que cuando entras hay jaulas a ambos lados del pasillo y vas escuchando tanto llantos como ladridos de los perros que slo quieren jugar y salir de lo que para ellos es una especie de prisin. Por ello, hay unas zonas en las que varios perros van saliendo a la vez para jugar y pasar un rato al aire libre y con mayor libertad, siempre teniendo en cuenta los perros que se llevan bien con otros perros o con las personas etc. Estuvimos toda la maana jugando con diferentes perros y sobre todo dndoles mucho cario, ya que es lo que ms necesitan en ese momento. Es increble ver cmo a pesar de lo mal que se han portado los humanos con ellos, simplemente se acercan a ti a darte cario y no guardan ningn tipo de rencor ni enfado sino todo lo contrario. Esta experiencia fue muy bonita ya que una vez ms tuve la oportunidad de ayudar a animales, pero a la vez creo que ha sido de las ms duras y difciles para m al principio. Sufr mucho al ver las condiciones en las que se encontraban y sent mucha impotencia al no poder hacer prcticamente nada por ayudarlos, ya que si no encuentran familia pronto tienen que ir abandonando la perrera y finalmente perdern su vida. Es muy triste ver eso sobre todo si tienes una pasin tan grande por los animales cmo lo es en mi caso, adems, mi perra tambin fue rescatada tras ser abandonada por otra familia y sent que seguramente habr sufrido igual que los perros que v yo en esta experiencia, por lo que aunque me llevo un recuerdo bastante triste, tambin me alegro de haberles podido alegrar la maana a algunos de esos perros.</p>
                <div className="actSlideContainer">
                    <Fade infinite={true}>
                        {slideImagesPerritos.map((slideImage, index)=> (
                            <div key={index}  className="actSlide">
                                <img src={slideImage} alt={slideImage} className='actSlideImg' />
                            </div>
                        ))} 
                    </Fade>
                </div>
            </div>

            <h1>CLASES DE BAILE EN EL RECREO</h1>
            <div className='act'>
                <p className='actParrafo'>Esta experiencia trata de realizar clases de baile durante el recreo y se ha expandido los dos aos de curso, en los cuales 1 o 2 das a la semana se realizaban en el saln de actos. Esto me ha ayudado a mejorar en dos mbitos de mi vida. El primero de ellos es la vergenza, ya que bailar delante del resto de mis compaeros ha hecho que consiguiera mayor confianza en m misma y dejara a un lado la vergenza que pudiera estar pasando para disfrutar y aprender con ellos. Adems de esto, tambin me ha ayudado a mejorar mi actividad fsica, ya que es el campo ms dbil que tengo y que ms necesitaba reforzar. Y no slo eso, sino que tambin he mejorado mi tcnica de baile y la coordinacin al practicar semana tras semana diferentes tipos de baile latino.</p>
                <div className="actSlideContainerVideo">
                    <Fade infinite={true}>
                        {slideVideosBaile.map((slideVideo, index)=> (
                            <div key={index}>
                                <div className='actSlideVideo'>
                                    <ReactPlayer 
                                    url={slideVideo}
                                    pip={true}
                                    controls={true}
                                    playing={false}
                                    />
                                </div>
                            </div>
                        ))} 
                    </Fade>
                </div>
            </div>

            <h1>CARRERA SOLIDARIA</h1>
            <div className='act'>
                <p className='actParrafo'>Esta experiencia consisti en una carrera a cambio de un kilo de comida, se realiz el 17 de diciembre en el parque de la Feria de Jerez. Esta experiencia fue difcil para m ya que tengo asma y esto dificulta mi rendimiento y resistencia fsica, sobre todo cuando se trata de una carrera. An as, prefer apoyar la causa y formar parte de esta actividad solidaria en la que disfrut mucho con mis amigos adems de sentirme realizada por recaudar numerosos kilos de alimentos. Esta experiencia me demostr que a pesar de ciertas dificultades, todos somos capaces de conseguir y realizar lo que nos propongamos mediante el esfuerzo y la voluntad.</p>
                <div className="actSlideContainerVideo">
                    <Fade infinite={true}>
                        {slideImagesCarrera.map((slideImage, index)=> (
                            <div key={index}  className="actSlide">
                                {isVideo(slideImage)}
                            </div>
                        ))}
                    </Fade>
                </div>
            </div>

            <h1>1 DA DE CROSSFIT</h1>
            <div className='exp'>
                <p className='expParrafo'>Esta experiencia consisti en asistir el 20 de febrero a una clase intensiva de crossfit, ya que mis amigas tenan un plan en el cual podan invitar gratuitamente a alguien para que probara. Estas clases son totalmente intensivas y para mi rendimiento, teniendo en cuenta mi problema asmtico, fueron demasiado. De hecho, a pesar del esfuerzo que le dediqu, tuve que parar casi al final de la clase por no poder continuar por el bien de mi salud. Esta experiencia me ayud a superarme nuevamente en el mbito fsico y de actividad y tambin me ense que a veces hay dificultades que te impiden llegar al final, pero que lo importante es esforzarse hasta el final e intentar dar el mximo de uno mismo. Adems, tambin valor el trabajo y esfuerzo que realizan todas las personas que practican este deporte y apreci lo difcil que este era aunque desde fuera no lo pareciera tanto.</p>
                <div className="expSlideContainerVideo">
                    <Fade  infinite={true} >
                        {slideVideosCrossfit.map((slideVideo, index)=> (
                            <div key={index} className='expSlide'>
                                <div className='expSlideVideo'>
                                    <ReactPlayer 
                                    url={slideVideo}
                                    pip={true}
                                    controls={true}
                                    playing={false}
                                    />
                                </div>
                            </div>
                        ))} 
                    </Fade>
                </div>
            </div>

            <h1>SALIDA PROCESIONAL</h1>
            <div className='exp'>
                <p className='expParrafo'>Esta experiencia consisti en una salida procesional de 4 horas y media el da 12 de abril de 2021. Esta experiencia fue muy gratificante ya que fue muestra de penitencia y tambin de superacin, en general, andar es de los deportes que menos me motivan o me llaman la atencin, pero muchas veces es algo necesario para la salud fsica y tambin mental. Por ello, no encontr mejor forma de superarme en este sentido que realizar la estacin de penitencia de mi hermandad, ya que adems de superarme, tambin hara feliz a una persona super importante para m, mi abuelo. De esta forma, esta experiencia ha servido para adentrarme an ms en el campo de actividad, que es el ms dbil desde mi punto de vista y adems fue una experiencia totalmente emotiva al hacer tan feliz a una de las personas que ms ha hecho por m.</p>
                <div className="expSlideContainer">
                    <Fade  infinite={true}>
                        {slideImagesSemanaSanta.map((slideImage, index)=> (
                            <div key={index}  className="expSlide">
                                <img src={slideImage} alt={slideImage} className='expSlideImg' />
                            </div>
                        ))}
                    </Fade>
                </div>
            </div>

            <h1>RECORRIDO EL BOSQUE</h1>
            <div className='exp'>
                <p className='expParrafo'>Esta experiencia se realiz el 18 de marzo y consisti en realizar una ruta de manera voluntaria. Esta experiencia me hizo conectar mucho con mis compaeros de clase y entablar mucho ms compaerismo entre nosotros. Estuvimos todo el da de ruta y realizamos algunas actividades juntos, hicimos bromas, hablamos y tambin comimos juntos. Esta experiencia me demostr la importancia de relacionarme con los dems ya que a pesar de mi asma y el clima que dificultaron el camino, prcticamente se me olvid entre risas y charla. Esta experiencia uni mucho a la clase y me sirvi para darme cuenta de que estamos todos juntos en esta aventura y en esta nueva experiencia; el Diploma de Bachillerato Internacional.</p>
                <div className="expSlideContainer">
                    <Fade  infinite={true}>
                        {slideImagesRutaBosque.map((slideImage, index)=> (
                            <div key={index}  className="expSlide">
                                <img src={slideImage} alt={slideImage} className='expSlideImg' />
                            </div>
                        ))}
                    </Fade>
                </div>
            </div>

            <h1>TEATRO EN MISA</h1>
            <div className='exp'>
                <p className='expParrafo'>Esta experiencia consisti en elaborar un teatro que mostrar la amistad y el amor para nios de entre 6-14 aos en la misa de los Salesianos Manuel Lora Tamayo y se realiz el 24 de abril. En ella, primero realizamos algunas manualidades para utilizarlas como smbolo en nuestro teatro adems de crear todo el dilogo y la escenificacin. Fue una experiencia muy bonita ya que era la primera vez que realizaba un teatro desde cero y gracias a la ayuda de mis amigos y la motivacin que me nace al ser algo para los nios, fue una escena preciosa en la que tanto los nios como nosotros los actores disfrutamos y a mi parecer sali increble. Me ilusion mucho haber sido capaz de crear un teatro desde cero y la buena retroalimentacin que recib tras hacerlo, ya que todos nos agradecieron el esfuerzo y la dedicacin empleada.</p>
                <div className="expSlideContainer">
                    <div className="expSlide">
                        <img src={imgVolun3} alt={imgVolun3} className='expSlideImg' />
                    </div>
                </div>
            </div>

            <h1>GIMNASIO</h1>
            <div className='exp'>
                <p className='expParrafo'>Esta experiencia consisti en estar asistiendo al gimnasio Activaclub 3 veces a la semana durante 3 meses (junio, julio y agosto). Esta experiencia ha sido de las ms importantes para mi ya que no realizaba nada de actividad fsica y esto afectaba tanto a mi salud fsica como a mi salud mental. Realizar deporte semanalmente me ayud a reducir el estrs y tambin lo acompae de una mejor alimentacin as que mejore mi fsico a uno ms equilibrado. Esta experiencia fue dura ya que mi asma y mi poca movilidad a veces dificultaba mis entrenamientos pero tambin fue muy positivo ver mi progreso da tras da y verme capaz de realizar actividad fsica de forma regular ya que durante el curso me es imposible compatibilizar la actividad con las actividades extraescolares y las clases. Por lo que esta experiencia ha sido de las ms significativas durante mi curso CAS.</p>
                <div className="expSlideContainer">
                    <Fade  infinite={true}>
                        {slideImagesGym.map((slideImage, index)=> (
                            <div key={index}  className="expSlide">
                                <img src={slideImage} alt={slideImage} className='expSlideImg' />
                            </div>
                        ))}
                    </Fade>
                </div>
            </div>

            <h1>CAMPAMENTO DE VERANO</h1>
            <div className='exp'>
                <p className='expParrafo'>Esta experiencia consisti en asistir a una campamento de f en el que realic numerosas yinkanas y actividades en equipo desde el 11 de julio al 18 de este mes. Realic muchas actividades diferentes pero me gustara destacar:</p>
                <ul className='listaCampamento'>
                    <li>
                        <h3>PARTIDO DE FTBOL</h3>
                        <div className='exp'>
                            <p className='expParrafo'>Era la primera vez que jugaba un partido de ftbol y hacerlo con personas que no conoca me hizo sentirme menos juzgada y dar lo mejor de m durante este. Por ello, fue muy gratificante el juego en equipo ya que hizo que mejorara mis habilidades sociales y tambin aprend mucho mi actividad y mis tcnicas en dicho deporte.</p>
                            <div className="expSlideContainer">
                                <Fade  infinite={true}>
                                    {slideImagesFutbol.map((slideImage, index)=> (
                                        <div key={index}  className="expSlide">
                                            <img src={slideImage} alt={slideImage} className='expSlideImg' />
                                        </div>
                                    ))}
                                </Fade>
                            </div>
                        </div>
                    </li>
                    <li>
                        <h3>GYMKANA POR GRUPOS</h3>
                        <div className='exp'>
                            <p className='expParrafo'>Esta trat de numerosos stands en los que haba que realizar retos diferentes, todos ellos en equipo. Lo realic con mi grupo en el cul cada uno pertenecamos a una provincia diferente o a una ciudad distinta. Por ello, formar parte de esto fue una oportunidad para conocer a personas nuevas y entablar relaciones muy bonitas. Adems, para conseguir los retos debamos participar todos y aportar cada uno de nuestra parte para superarlo y pasar al siguiente, por ello sent que mi ayuda fue igual de necesaria que la del resto y pude valorar una vez ms la importancia del trabajo en equipo, aunque se trate de personas desconocidas.</p>
                            <div className="expSlideContainer">
                                <Fade  infinite={true}>
                                    {slideImagesGymkana.map((slideImage, index)=> (
                                        <div key={index}  className="expSlide">
                                            <img src={slideImage} alt={slideImage} className='expSlideImg' />
                                        </div>
                                    ))}
                                </Fade>
                            </div>
                        </div>
                    </li>
                    <li>
                        <h3>AQUAGYM</h3>
                        <div className='exp'>
                            <p className='expParrafo'>Consisti en realizar dos horas de aquagym en la piscina del pueblo. Esta experiencia me ayud tanto con mi actividad fsica, ya que fue algo totalmente nuevo y divertido ya que no lo haba realizado nunca y pas un buen rato con mis nuevos amigos, como con mi timidez, ya que realizamos esta actividad delante del resto de personas que asistieron a la piscina pblica ese da, por lo que tuve que dejar la vergenza de lado y pasar un buen rato con mis amigos sin preocuparme por ello.</p>
                            <div className="expSlideContainerVideo">
                                <Fade  infinite={true} >
                                    {slideVideosAquagym.map((slideVideo, index)=> (
                                        <div key={index} className='expSlide'>
                                            <div className='expSlideVideo'>
                                                <ReactPlayer 
                                                url={slideVideo}
                                                pip={true}
                                                controls={true}
                                                playing={false}
                                                />
                                            </div>
                                        </div>
                                    ))} 
                                </Fade>
                            </div>
                        </div>
                    </li>
                </ul>
            </div>

            <h1>DECORACIN DE NAVIDAD</h1>
            <div className='exp'>
                <p className='expParrafo'>Se realiz el 10 de diciembre y consisti en decorar la casa de mis suegros como sorpresa de Navidad. Puede parecer algo que realiza cualquier familia en estas fechas pero me encargu de que fuera lo ms bonito posible. Para ello, tuve que hacer uso de una escalera, a pesar de mi pnico a las alturas; al hacerlo por otra persona siempre intento superarme y as fue, consegu superar algunos de mis miedo y me atrev a subirme y decorar el rbol. Decoramos la casa entera, incluido el tejado e hicimos muchos adornos de forma artesanal con objetos que encontramos o compramos, fue una experiencia larga ya que tuvimos que decidir dnde colocar cada adorno, buscar un momento en el que no hubiese nadie en la casa y adems, dedicarle la tarde entera ya que hubo complicaciones, algunas luces no encendan etc. Al final conseguimos que se emocionaran y me sent muy orgullosa del trabajo y esfuerzo que le dediqu, ya que super mi miedo a las alturas o al menos lo afront y adems realice manualidades que quedaron muy bien como decoracin.</p>
                <div className="expSlideContainer">
                    <Fade  infinite={true}>
                        {slideImagesDecoracion.map((slideImage, index)=> (
                            <div key={index}  className="expSlide">
                                <img src={slideImage} alt={slideImage} className='expSlideImg' />
                            </div>
                        ))}
                    </Fade>
                </div>
            </div>

            <h1>GRUPOS DE FE</h1>
            <div className='exp'>
                <p className='expParrafo'>Adems de ser animadora de patio en el Centro Juvenil Futuro Abierto, tambin coordino grupos de fe a un grupo de entre 10-11 aos desde septiembre hasta el da de hoy. Esta experiencia ha sido de las ms bonitas durante estos dos aos ya que he creado un vnculo muy especial con ellos y he aprendido muchas cosas a la vez que enseado valores esenciales. Estos grupos se realizan todos los viernes de 5 y media a 6 y media, y cada semana tengo que preparar y organizar un grupo con temticas diferentes, pueden ser manualidades, reflexiones e incluso juegos o gymkanas. Muchas veces es frustrante ya que es una labor que realizo de forma totalmente voluntaria y recibo contestaciones o comportamiento grosero por parte de los nios, pero por otro lado as puedo darme cuenta de lo difcil que es ensear a veces y de esta forma he conseguido ponerme en la piel de mis profesores y entender muchas veces sus cabreos o broncas. Adems de esto, tambin considero que es una gran responsabilidad ya que aunque sea de manera voluntaria, este grupo cuenta conmigo viernes tras viernes y no es algo que pueda dejar de repente o no asistir un da simplemente porque no me apetece, soy consciente del compromiso que esto requiere y de que a pesar de que haya veces que este ms o menos ilusionada, todos los das tengo que dar lo mejor de m para que esos nios reciban un mensaje bonito y aprendan algo nuevo. Por ello, esta experiencia es tan importante tanto para mi como para mi proyecto CAS en general, ya que he adquirido muchas cualidades gracias a mis alumnos y tambin me he implicado mucho en la elaboracin de las actividades para cada viernes, a pesar de los exmenes que tuviera o lo ocupada que estuviera esa semana.</p>
                <div className="expSlideContainer">
                    <Fade  infinite={true}>
                        {slideImagesGrupos.map((slideImage, index)=> (
                            <div key={index}  className="expSlide">
                                <img src={slideImage} alt={slideImage} className='expSlideImg' />
                            </div>
                        ))}
                    </Fade>
                </div>
            </div>

            <h1>RECOGIDA DE JUGUETES</h1>
            <div className='exp'>
                <p className='expParrafo'>Esta experiencia consisti en el proyecto de una compaera el cual se caracteriz por la recogida de juguetes con vistas a la navidad los das previos a las vacaciones. En ella, mis compaeros y yo nos encargamos de dar propaganda al proyecto y una vez finalizada la fecha, de ordenar todos los juguetes, desechar los que no servan o les faltaban piezas y empaquetarlos de la mejor manera posible. Fue una labor totalmente en equipo ya que mientras uno organizaba el otro empaquetaba y viceversa y gracias a todos pudimos amenizar mucho el trabajo. Fue una experiencia preciosa y tambin reflexiva ya que nunca me haba parado a pensar en la cantidad de juguetes que tenemos en casa y ya no utilizamos y a la vez la cantidad de nios que no reciben juguetes por navidad ya sea por una u otra razn. De esta manera, esta experiencia me sirvi para concienciarme y darme cuenta de la importancia de compartir y donar siempre que haga falta.</p>
                <div className="expSlideContainer">
                    <Fade  infinite={true}>
                        {slideImagesRecogidaJuguetes.map((slideImage, index)=> (
                            <div key={index}  className="expSlide">
                                <img src={slideImage} alt={slideImage} className='expSlideImg' />
                            </div>
                        ))}
                    </Fade>
                </div>
            </div>
        </div>
    );
}

export default Actividad;
